	module.exports = {"data":{"markdownRemark":{"html":"<pre class=\"pre\"><code class=\"language-playground_norender\">class App extends React.Component {\n  constructor() {\n    super();\n    this.state = {\n      clicked: false,\n      style: {\n        data: { fill: &quot;tomato&quot; }\n      }\n    };\n  }\n\n  render() {\n    const handleMouseOver = () =&gt; {\n      const fillColor = this.state.clicked ? &quot;blue&quot; : &quot;tomato&quot;;\n      const clicked = !this.state.clicked;\n      this.setState({\n        clicked,\n        style: {\n          data: { fill: fillColor }\n        }\n      });\n    };\n\n    return (\n      &lt;div&gt;\n        &lt;VictoryChart height={400} width={400}\n          domainPadding={{ x: 50, y: [0, 20] }}\n          scale={{ x: &quot;time&quot; }}\n        &gt;\n          &lt;VictoryBar\n            dataComponent={\n              &lt;Bar events={{ onMouseOver: handleMouseOver }}/&gt;\n            }\n            style={this.state.style}\n            data={[\n              { x: new Date(1986, 1, 1), y: 2 },\n              { x: new Date(1996, 1, 1), y: 3 },\n              { x: new Date(2006, 1, 1), y: 5 },\n              { x: new Date(2016, 1, 1), y: 4 }\n            ]}\n          /&gt;\n        &lt;/VictoryChart&gt;\n      &lt;/div&gt;\n    );\n  }\n }\n\nReactDOM.render(&lt;App/&gt;, mountNode);</code></pre>","frontmatter":{"id":0,"scope":null,"title":"Alternative Events"},"fields":{"slug":"/gallery/alternative-events/"}}},"pathContext":{"slug":"/gallery/alternative-events/"}}

